/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem;
  font-weight: 900;
  color: #0da6f3;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

body {
  background-color: #1a1b15;
}

@media (min-width: 768px) {
  .contenedor-peluqueria {
    height: 100vh;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-peluqueria .imagen {
  background-image: url("../img/1.webp");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 30rem;
}
@media (min-width: 768px) {
  .contenedor-peluqueria .imagen {
    height: 1024px;
  }
}
.contenedor-peluqueria .app {
  width: 95%;
  margin: 0 auto;
  padding: 2.5rem;
}
.contenedor-peluqueria .app .seccion {
  display: none;
  margin-top: 3rem;
}
.contenedor-peluqueria .app .mostrar-seccion {
  display: block;
}

.tabs {
  display: grid;
  gap: 2.5rem;
  grid-template-columns: repeat(3, 1fr);
}
.tabs button {
  padding: 1rem 0;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #FFFFFF;
  outline: none;
  border: none;
}
.tabs button.activo {
  background-color: #0da6f3;
  color: #FFFFFF;
}
.tabs button:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 1.5rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.listado-servicios .servicio :hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .listado-servicios .servicio {
    margin-bottom: 0;
  }
}
.listado-servicios .servicio p {
  margin: 0;
}
.listado-servicios .servicio .nombre-servicio {
  color: #1a1b15;
  font-size: 1.8rem;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.8rem;
  margin-top: 1rem;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}

.paginacion {
  display: flex;
  justify-content: space-between;
  margin: 5rem 0;
}
.paginacion button {
  padding: 1rem 2rem;
  background-color: #0da6f3;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.6rem;
  text-transform: uppercase;
  border: none;
  border-radius: 0.5rem;
}
.paginacion button:hover {
  cursor: pointer;
}
.paginacion .oculto {
  visibility: hidden;
}

.formulario {
  margin-top: 3rem;
  width: 100%;
}
.formulario .formulario__grupo {
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
}
.formulario .formulario__grupo label {
  flex: 0 0 10rem;
  color: #FFFFFF;
  margin-bottom: 0.5rem;
  font-size: 2rem;
}
.formulario .formulario__grupo input {
  flex: 1;
  padding: 1rem;
  border: none;
  outline: none;
  border-radius: 1rem;
}

.alerta {
  color: #FFFFFF;
  padding: 1rem;
  margin: 5rem;
  border-radius: 5rem;
  text-align: center;
}
.alerta.error {
  background-color: #dc143c;
}

.invalidar-cita {
  font-size: 2rem;
  text-align: center;
  margin: 3rem 0;
  text-transform: uppercase;
  color: #dc143c;
}